import { FunctionComponent, useContext } from "react";
import styled from 'styled-components';
import { AppContext } from "../context/AppContext";

const AppHeaderWrap = styled.header`
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    color: #fff;
    height: var(--header-height, 3rem);
    background: #003349;
    display: flex;
    align-items: center;
    padding: 0 2rem;
    z-index: 999999;
    @media print {
        position: relative;
    }
`;

const AppHeaderImg = styled.img`
    margin-top: .2rem;
    height: 1.3rem;
    width: auto;
`;

const AppHeaderTitle = styled.span`
    font-weight: 700;
`;

const VerticalRule = styled.span`
    background-color: #335C6D;
    width: 1px;
    height: 24px;
    margin: auto 1rem;
`;

export const AppHeader: FunctionComponent = () => {
    const { options } = useContext(AppContext);
    if (!options.showHeader) {
        return null;
    }
    return <AppHeaderWrap>
        <AppHeaderImg
            alt="Sage"
            src="/dls-release-notes/images/logo-image.svg"
        />
        <VerticalRule />
        <AppHeaderTitle>{options.productName}</AppHeaderTitle>
    </AppHeaderWrap>;
}